header {
    /* height: 100vh; */
    padding-top: 3rem;
    /* overflow: hidden; */
}

.header__container {
    text-align: center;
    height: 85%;
    position: relative;
}

.cta {
    margin-top: 1.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
    cursor: pointer;
}

.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 5rem;
}

.header__socials a {
    font-size: 2rem;
}

.header__socials::after {
    content: "";
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

.me {
    /* background: linear-gradient(var(--color-primary), transparent); */
    width: 15rem;
    height: 20rem;
    /* position: absolute; */
    /* display: flex;
    align-items: center; */
    /* left: calc(50% - 7.5rem); */
    margin-top: 2rem;
    margin: 2rem auto 0;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    /* padding: 5rem 1.5rem 1.5rem 1.5rem; */
}

.scroll-down {
    position: absolute;
    right: 2.3rem;
    bottom: 8rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

/* @media screen and (max-width: 1024px) {
    header {
        height: 68vh;
    }
} */

@media screen and (max-width: 600px) {
    /* header {
        height: 100vh;
    } */

    .header__socials,
    .scroll-down {
        display: none;
    }
}